.scroll_page--scroll1 {
  width: 400px;
  height: 200px; }
.scroll_page--scroll2 {
  width: 400px;
  height: 300px; }
  .scroll_page--scroll2 .scroller_content {
    width: 800px; }
.scroll_page--scroll3, .scroll_page--textarea {
  width: 400px;
  height: 200px;
  float: left; }

#modal-layout .modal {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  overflow: auto;
  display: block;
  right: auto;
  bottom: auto;
  z-index: 101; }
#modal-layout .modal-header {
  text-align: center; }
  #modal-layout .modal-header .close {
    float: right; }
#modal-layout .modal-dialog {
  background: white;
  display: table;
  position: relative;
  min-width: 60%;
  margin: 10px auto; }
#modal-layout .modal-content {
  position: relative;
  display: table-cell;
  vertical-align: middle;
  border-radius: 0;
  width: 100%;
  height: 100%;
  background: none;
  box-shadow: none;
  border: none; }
#modal-layout .modal-body {
  max-height: 100%;
  padding: 0; }
#modal-layout .modal-title {
  text-align: center; }

.modal-backdrop.in {
  opacity: 0.9;
  z-index: 100; }

.scroller_component {
  position: relative;
  overflow: hidden; }
  .scroller_component .scroller {
    width: 100%;
    height: 100%;
    border: 0; }
    .scroller_component .scroller[data-baron-v="inited"] {
      overflow-y: scroll;
      box-sizing: content-box;
      padding-right: 16px; }
      .scroller_component .scroller[data-baron-v="inited"] .scroller__track {
        right: 4px;
        top: 10px;
        bottom: 4px;
        width: 10px; }
      .scroller_component .scroller[data-baron-v="inited"] .scroller__bar {
        right: 0;
        width: 10px; }
    .scroller_component .scroller[data-baron-h="inited"] {
      overflow-x: scroll;
      box-sizing: content-box;
      padding-bottom: 16px; }
      .scroller_component .scroller[data-baron-h="inited"] .scroller__track {
        right: 4px;
        left: 4px;
        bottom: 4px;
        height: 10px; }
      .scroller_component .scroller[data-baron-h="inited"] .scroller__bar {
        height: 100%; }
    .scroller_component .scroller::-webkit-scrollbar {
      width: 0; }
    .scroller_component .scroller .scroller__track {
      display: none;
      position: absolute;
      background: rgba(0, 0, 0, 0.1); }
    .scroller_component .scroller.baron .scroller__track {
      display: block; }
    .scroller_component .scroller .scroller__bar {
      position: absolute;
      z-index: 1;
      background: #999; }
